.header__bottom-ad{
  width: 100%;
  float:left;
  display: block;
}
.ad-search{
  width: 80%;
  float: left;
  display: block;
  position: relative;
}
.search-pic{
  background-image: url('../img/adsearchpic.png');
  position: absolute;
  background-repeat: no-repeat;
  display: block;
  float: left;
  width: 20px;
  height: 23px;
  z-index: 2;
  left: 25px;
  top: 7px;

}
.input-search-ad{
  float: left;
  display: inline-block;
  height: 37px;
  width: 90%;
  line-height: 37px;
  background-color: #fff;
