}
.delivery_block{
width: 20%;
padding:  0;
background-color: #334a51;
font-size: 11px;
display: block;
float: left;
position: relative;
}
.delivery_block .delivery_list {
padding: 0px 6px 3px 6px;
height:35px;
cursor: pointer;
margin: 0 20px 0 0;
color: #ed4226;
background-color: #334a51;
width: 100%;
z-index: 11;

}
.delivery_list #btn {
		width: 0;
		height: 0;
  	border-left: 7px solid transparent;
  	border-right: 7px solid transparent;
  	border-top: 12px solid #ffd800;
		float: right;
		position: absolute;
		right: 5%;
		top: 10px;

}
.delivery_list span {
padding: 2px 0 0 20px;
display: block;
font-weight: bold;
font-size: 16px;
font-family: $osb;
color: #fff;
height: 35px;
line-height: 30px;
}
.delivery_block ul.cities_list {
margin-top: 0px;
z-index: 10;
background-color: #fff6c4;
position: absolute;
width: 100%;
padding: 0;

display: none;
}
ul.cities_list li {
padding: 6px 0px 6px 30px;
list-style-type: none;
font-family: $or;
font-size: 16px;
color: #334a51;
position: relative;
&:before{
	content: "";
	position: absolute;
	background-image: url("../img/disk.png");
	width: 5px;
	height: 5px;
	margin-left: -20px;
  margin-top: 10px;

}

}
ul.cities_list li:hover {
cursor: pointer;
background: #fff;
}
.delivery_block .delivery_text {
padding: 3px 5px;
margin: 5px 0 0 3px;
}
.yellow-block{
	width: 80%;
	float: left;
	display: block;
	background-color: #ffd800;
	height: 35px;
}
.adcontent-main{
	width: 100%;
	float: left;
	display: block;
}
.ad-charasteristics{
	width: 20%;
	float: left;
	display: block;
	position: relative;
	z-index: 2;
	@include breakpoint(max-width 992px){
		width: 25%;
	}
	@include breakpoint(max-width 770px){
		display: none;
		width: 250px;
		margin: 0 auto;
		float: none;
	}

}
.ad-charasteristics__mob-btn{
	display: none;
	@include breakpoint(max-width 670px){
		width: 100%;
		display: inline-block;
		text-align: center;
		width: 100%;
		float: left;
		margin-right: 20px;
		background-color: #334a51;
		border-radius: 20px;
		font-family: OpenSansBold;
		font-size: 12px;
		text-transform: uppercase;
		color: #fff;
		text-decoration: none;
		padding: 8px 10px;
		line-height: 20px;
		box-sizing: border-box;
		margin: 10px 0;
	}
}
#sidebar-filter{
	margin: 0;
	top: 45px;
	position: -webkit-sticky;
	position: sticky;

}
.ad-charasteristics-form{
	width: 100%;
	float: left;
	display: block;
	position: relative;
	padding: 0 15px 5px 15px;
	transition: transform 1s ease-out;
	.apply-button{
		background-color: $green;
		color: #fff;
		font-family: $ob;
		font-size: 14px;
		margin: 10px auto;
		display: inline-block;
		float: none;
		border: none;
		border-radius: 20px;
		padding: 5px 10px;
		box-sizing: border-box;
		cursor: pointer;
		outline: none;
		left: 21%;
		position: relative;
		@include green-hov-rgba                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                         ;
	}
	@include breakpoint(max-width 670px){
		display: none;
	}
}
