.fixed_addpage{
	position: fixed;
	left: 0;
	margin: 0;
	padding: 5px 0;
	box-sizing: border-box;
	background-color: #fff;
	transition: transform 1s ease-out;
}
.right-sidebar{
	float: left;
	display: block;
	width: 27%;
	padding-left: 10px;
	box-sizing: border-box;
	margin-bottom: 20px;
	.banner-rightbar{
		float: right;
	}
	@include breakpoint(max-width 992px){
		display: none;
	}
}
.average-ad-item, .ad-author__content_left-item{
	width: 100%;
	float: left;
	display: flex;
	padding: 11px 0 11px 1px;
	border-bottom: 1px solid #dedede;
}
.average-ad-item {
	display: flex;
	@include breakpoint(max-width 770px) {
		flex-direction: column;
		width: 49%;
		&:nth-of-type(2n+2) {
			margin-right: 2%;
		}
	}
	@include breakpoint(max-width 500px) {
		width: 100%;
		margin: 0!important;
	}
}
.average-ad-item-thumb{
	width: 25%;
	float: left;
	display: block;
	height: 124px;
	position: relative;
	>img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	>span {
		display: block;
		height: 50px;
		width: 50px;
		position: absolute;
		top: -10px;
		right: -10px;
		@include breakpoint(max-width 500px) {
			width: 65px;
			height: 65px;
			top: 0;
			right: 0;
		}
		img {
			height: 100%;
			width: 100%;
			display: block;
			object-fit: cover;
		}
	}
	@include breakpoint(max-width 770px){
		width: 100%;
		height: 150px;
	}
}
.average-ad-item-content{
	width: 75%;
	float: left;
	display: flex;
	flex-direction: column;
	position: relative;
	padding-left: 15px;
	@include breakpoint(max-width 770px){
		width: 100%;
		padding-left: 0;
	}
}
.average-ad-time{
	width: 100%;
	float: left;
	display: inline-block;
	color: #878787;
	font-family: $or;
	font-size: 12px;
	margin: 0;
}
.average-ad-category{
	font-size: 12px;
	font-family: $or;
	color: $link;
	margin: 0 4px;
	float: left;
	display: inline-block;
	&:first-of-type{
		margin-left: 0!important;
	}
}
.bottom-content{
	width: 100%;
	position: relative;
	margin-top: auto;
}
.top-content{
	width: 100%;
	float: left;
	position: relative;
	display: flex;
	@include breakpoint(max-width 770px) {
		flex-direction: column-reverse;
	}
}
.top-content-left {
	width:calc(100% - 95px);
	padding-right: 15px;
	@include breakpoint(max-width 770px) {
		width: 100%;
	}
}
.top-content-right {
	width: 95px;
	display: flex;
	flex-direction: column;
	@include breakpoint(max-width 770px) {
		flex-direction: row;
		width: 100%;
		margin: 10px 0;
	}
	>span {
		padding-bottom: 10px;
		display: flex;
		align-items: center;
		img {
			padding-right: 5px;
			height: 17px;
			width: 17px;
			object-fit: contain;
		}
		span {
			font-size: 9px;
			@include breakpoint(max-width 770px) {
				display: none;
			}
		}
	}
	.average-ad-price {
		margin-right: auto;
	}
}
.average-ad-category:first-of-type{
	margin-left: 0!important;
}
.average-ad-title, .vip-ad-title{
	float: left;
	width: 100%;
	font-size: 18px;
	font-family: $ob;
	color: #334a51;
	text-decoration: none;
	text-align: left;
	//margin-top: 15px;
	@include breakpoint(max-width 770px) {
		width: 100%;
	}
}
.vip-ad-title{
	color: #1a97bb;
	font-size: 16px;
	width: 100%;
}
