.ui-slider .ui-slider-handle{
	background-color: $green;
	cursor: pointer;
	outline: none;
	width: 11px;
	height: 12px;
	position: absolute;
	top: 11px;
	&::before{
		content: "";
		width: 0;
		height: 0;
		position: absolute;
		margin-top: -10px;
		border-left: 6px solid transparent;
		border-right: 5px solid transparent;
		border-bottom: 10px solid $green;
	}
}
.ui-slider-horizontal{
	cursor: pointer;
}
