.shops-header__menu{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  background-color: #334a51;
}
.shops-header__mnu{
  width: 100%;
  padding: 10px 0 0;
  box-sizing: border-box;
  display: inline-block;
  float: left;
  text-align: left;
  margin: 0;
  li{
    display: inline-block;
    list-style-type: none;
    min-width: 5%;
    margin: 0 20px;
    padding-bottom: 18px;
    box-sizing: border-box;
    position: relative;
    a{
      text-decoration: underline;
      color: #fff;
      font-family: $ob;
      font-size: 14px;
      width: 100%;
      display: inline-block;
      position: relative;
      .icon{
        background-repeat: no-repeat;
        // background-size: cover;
        position: relative;
        margin-right: 5px;
        display: inline-block;
      }
    }
    &:first-of-type{
      margin-left: 0;
    }
  }
  li.active{
    // a{
    //   border-bottom: 3px solid $bezh;
    // }
    a::after{
      content: "";
      position: absolute;
      height: 4px;
      width: 100%;
      left: 0;
      background-color: #ffd800;
      display: inline-block;
      top: 42px;
    }
    // a{
    //   color: #ffdf2d;
    //   text-decoration: none;
    // }
  }
}
.shops-ad, .shops-favorite, .shops-my, .shops-msg, .shops-wallet, .shops-setting,{
    display: inline-block;
    width: 26px;
    height: 23px;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
    top: 6px;
    margin-right: 5px;
}
.shops-ad-icon, .about-company, .jobs-icon_shop, .comments-icon_shop, .special-offer{
  display: inline-block;
  size: 21px 23px;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
  top: 7px;
  margin-right: 5px;
}
.shops-ad-icon{
  background-image: url("../img/shops-header-icon/ad-shoops.png");
}
.special-offer{
  background-image: url("../img/shops-header-icon/percentage.png");
}
.about-company{
  background-image: url("../img/shops-header-icon/about-company.png");
}
.comments-icon_shop{
  background-image: url("../img/shops-header-icon/edit-doc.png");
}
.jobs-icon_shop{
  background-image: url("../img/shops-header-icon/head.png");
}
.shops-ad{
  background-image: url("../img/menu-icon/shops-ad.png");
}
.shops-favorite{
  background-image: url("../img/menu-icon/shops-favorite.png");
}
.shops-my{
  background-image: url("../img/menu-icon/shops-my.png");
}
.shops-msg{
  background-image: url("../img/menu-icon/shops-msg.png");
}
.shops-wallet{
  background-image: url("../img/menu-icon/shops-wallet.png");
}
.shops-setting{
  background-image: url("../img/menu-icon/shops-setting.png");
}
.header-shop-fon{
  width: 100%;
  height: auto;
  float: left;
  position: relative;
  img{
    height: 122px;
    width: 100%;
    overflow: hidden;
    object-fit: cover;
  }
  .about-shop{
    width: 100%;
    float: left;
    position: relative;
    text-align: center;
    padding: 7px 0;
    box-sizing: border-box;
    h2{
      display: inline-block;
      margin: 0;
      padding: 0;
      font-size: 24px;
      font-family: $osb;
      color: #s;
      float: left;
      width: 33%;
      text-align: left;
    }
    p{
      float: left;
      display: inline-block;
      font-size: 18px;
      font-family: $ob;
      margin: 0;
      color: #000;
      width: 33%;
      line-height: 26px;
      span{
        padding-left: 10px;
        box-sizing: border-box;
        font-size: 12px;
        color: #646464;
        font-family: $or;
      }
      &:last-of-type{
        font-size: 14px;
      }
    }
  }
  .shop-discount{
    width: 100%;
    float: left;
    display: inline-block;
    background-color: #d4b50a;
    text-align: center;
    padding: 15px 0;
    box-sizing: border-box;
    p{
      margin: 0;
      font-size: 14px;
      font-family: $or;
      color: #000;
    }
  }
}
.header-shop_bottom{
  width: 100%;
  float: left;
  position: relative;
  .search-map{
    background-image: url("../img/search-map-long.png");
  }
  .nachalo{
    background-color: #fff;
  }
}
.shop-content{
  width: 100%;
  float: left;
  position: relative;
  &__left{
    width: 65%;
    float: left;
    position: relative;
    &_item{
      width: 100%;
      float: left;
      position: relative;
      border-bottom: 1px solid #dedede;
      padding: 10px 0;
      box-sizing: border-box;
      &:last-of-type{
        border-bottom: none;
      }
    }
  }
  &__right{
    width: 35%;
    float: left;
    position: relative;
    padding-left: 100px;
    box-sizing: border-box;
    text-align: center;
    p{
      text-align: left;
      width: 100%;
      font-family: $or;
      font-size: 12px;
      color: $green;
      background-color: white;
      margin-bottom: 6px;
      display: block;
      position: relative;
      float: left;
      span{
        font-family: $ob;
        color: $green;
      }
    }
    h2{
      width: 100%;
      display: block;
      position: relative;
      float: left;
      margin: 0;
      font-size: 14px;
      font-family: $ob;
      color: $green;
      padding: 10px 0 10px 15px;
      box-sizing: border-box;
      background-color: $bezh;
      text-align: left;
    }
    .mini-social{
      margin: 15px 0 18px;
    }
    .header__top_user{
      width: 100%;
      margin: 8px 0;
    }
    .user-pic{
      float: left;
    }
    .user-name{
      float: left;
      text-align: left;
    }

    .banner-1{
      float: left;
      margin-top: 0;
    }
    .favorite-shop-button, .mapsearch-shop-button, .complain-shop-button{
      display: inline-block;
      width: 100%;
      text-decoration: none;
      border: 1px solid #b5b5b5;
      border-right: none;
      border-top-left-radius: 14px;
      border-bottom-left-radius: 14px;
      padding: 15px;
      box-sizing: border-box;
      font-family: $ob;
      font-size: 12px;
      color: $green;
      font-weight: 600;
      padding: 7px 0 7px 15px;
      line-height: 19px;
      margin: 3px 0;
      text-align: left;
      .average-ad-star{
        float: left;
        top: 2px;
      }
    }
    .favorite-shop-icon, .geo-shop-icon, .complain-icon{
      display: inline-block;
      margin-right: 8px;
      background-image: url("../img/star-button.png");
      size: 16px;
      top: 3px;
      position: relative;
      background-repeat: no-repeat;
      background-size: contain;
    }
    .geo-shop-icon{
      background-image: url("../img/big-geo.png");
    }
    .complain-icon{
      background-image: url("../img/alert-icon.png");
    }
    .write-author{
      // width: 64%;
      width: 100%;
      height: 35px;
      line-height: 27px;
      text-align: left;
      margin: 6px auto 15px;
      display: inline-block;
      float: none;
      .open-mail{
        margin-right: 7px;
        top: 3px;
        position: relative;
      }
    }
  }
}
.logo-organization-wrap{
  width: 170px;
  height: 119px;
  position: absolute;
  left: 4%;
  overflow: hidden;
  margin-top: -122px;
  img{
    size: 100%;
    object-fit: contain;
  }
  @include breakpoint(min-width 1900px){
    left: 19%;
  }
}
